<template>
  <div class="sel-resolution">
    <vx-header :showback="1" :title="$t('settings.language')"></vx-header>
    <!-- 语言列表 -->
    <div class="vx-container">
      <div class="sel-sources">
        <vx-cell :class="{selected: languages[langIndex].id === o.id}" :icon="o.icon" :title="o.title" :showLink="false" v-for="(o,i) in languages" :key="i" @link="update(i)"></vx-cell>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapMutations, mapGetters } from 'vuex';
  import { Toast } from 'mint-ui';

  export default {
    data() {
      return {
        langIndex: 0,
        languages: [
          { title: this.$t('settings.chinese'), id: 1, name: 'zh' },
          { title: this.$t('settings.english'), id: 0, name: 'en' }
        ]
      };
    },
    created() {
      this.langIndex = Number(this.getLang === this.languages[1].name);
    },
    computed: {
      ...mapGetters(['getLang'])
    },
    methods: {
      ...mapMutations(['setLang']),
      update(index) {
        this.langIndex = index;
        this.setLang(this.languages[index].name);
        index ? window.Android.setEnglish() : window.Android.setChinese();
        // Toast(this.$t('common.NextEntryIntoForce'));
        window.location.reload();
      }
    }
  };
</script>

<style scoped lang="less">
  .sel-sources {
    padding-top: 25px;
    .vc-cell-title {
      color: #dddddd;
    }
    .vc-cell {
      border-bottom: 1px solid #4f5054; /*no*/
      &:last-child {
        border-bottom: none;
      }
    }
    .selected {
      background: ~"url('../../assets/common/checked.png') no-repeat" #383940;
      background-size: 32px 32px;
      background-position: calc(~'100% - 35px');
    }
  }
</style>


